import prev from '../img/prev.png'
import next from '../img/next.png'

const getTemplate = () => {
    return `
    <div class="backdrop" data-type="backdrop"></div>
    <div class="input" data-type="input"></div>
        <div class="dropdown">
            <div class="header">
                <button class="before" data-type="before"><img src=${prev} data-type="before"></button>
                <div class="mounth" data-type="mounth">April</div>
                <button class="next" data-type="next"><img src=${next} data-type="next"></button>
            </div>
        <div class="content" data-type="content"></div>
    </div>
    `
}

const mounts = [
    '–Ø–Ω–≤–∞—Ä—å',
    '–§–µ–≤—Ä–∞–ª—å',
    '–ú–∞—Ä—Ç',
    '–ê–ø—Ä–µ–ª—å',
    '–ú–∞–π',
    '–ò—é–Ω—å',
    '–ò—é–ª—å',
    '–ê–≤–≥—É—Å—Ç',
    '–°–µ–Ω—Ç—è–±—Ä—å',
    '–û–∫—Ç—è–±—Ä—å',
    '–ù–æ—è–±—Ä—å',
    '–î–µ–∫–∞–±—Ä—å',
]

export class DatePicker {
    // —ç—Ç–æ –∫–æ–Ω—Å—Ç—Ä—É–∫—Ç–æ—Ä –∫–ª–∞—Å—Å–∞. –¢–æ –µ—Å—Ç—å –æ–Ω –≤—ã–∑—ã–≤–∞–µ—Ç—Å—è –ø—Ä–∏ —Å–æ–∑–¥–∞–Ω–∏–∏ –æ–±—ä–µ–∫—Ç–∞
    constructor(selector, options) {
        // $ - —ç—Ç–æ –∑–Ω–∞—á–∏—Ç, —á—Ç–æ —ç—Ç–æ node —ç–ª–µ–º–µ–Ω—Ç DOM
        this.$el = document.querySelector(selector)

        this.options = options

        //—Ç–µ–∫—É—â–∏–µ –∑–Ω–∞—á–µ–Ω–∏—è
        const d = new Date()
        this.year = d.getFullYear()
        this.mounth = d.getUTCMonth()
        this.day = d.getDate()

        this.calcDates()

        //–ü–æ—Å–ª–∫ —Ç–æ–≥–æ, –∫–∞–∫ —Å–æ–∑–¥–∞–ª–∏, –Ω–∞—Ä–∏—Å–æ–≤–∞–ª–∏
        //–í–ê–ñ–ù–û! –ù–∞–ø–∏—Å–æ–≤–∞–ª–∏ != –≤–∏–¥–Ω–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—é
        this.#render()
        this.#setup()
    }

    //–ú–µ—Ç–æ–¥ –æ—Ç–∫—Ä—ã–≤–∞—é—â–∏–π/–ø–æ–∫–∞–∑—ã–≤–∞—é—â–∏–π –∫–∞–ª–µ–Ω–¥–∞—Ä—å
    open() {
        //—Ç–æ –µ—Å—Ç—å –º—ã –æ–±—Ä–∞—â–∞–µ–º—Å—è –∫ –Ω–∞—à–µ–º—É —ç–ª–µ–º–µ–Ω—Ç—É
        //—É –Ω–µ–≥–æ –∫ –ø–∞—Ä–∞–º–µ—Ç—Ä—É –∫–ª–∞—Å—Å–æ–≤(—Å—Ç–∏–ª–µ–π/—Å–µ–ª–µ–∫—Ç–æ—Ä–æ–≤)
        //–∏ –¥–æ–±–∞–≤–ª—è–µ–º —Ç—É–¥–∞ open
        this.$el.classList.add('open')

        //
    }

    //–ú–µ—Ç–æ–¥ –∑–∞–∫—Ä—ã–≤–∞—é—â–∏–π/—Å–∫—Ä—ã–≤–∞—é—â–∏–π –∫–∞–ª–µ–Ω–¥–∞—Ä—å
    close() {
        //—Ç–æ –µ—Å—Ç—å –º—ã –æ–±—Ä–∞—â–∞–µ–º—Å—è –∫ –Ω–∞—à–µ–º—É —ç–ª–µ–º–µ–Ω—Ç—É
        //—É –Ω–µ–≥–æ –∫ –ø–∞—Ä–∞–º–µ—Ç—Ä—É –∫–ª–∞—Å—Å–æ–≤(—Å—Ç–∏–ª–µ–π/—Å–µ–ª–µ–∫—Ç–æ—Ä–æ–≤)
        //–∏ —É–±–∏—Ä–∞–µ–º –æ—Ç—Ç—É–¥–∞ open
        this.$el.classList.remove('open')
    }

    select() {
        //
        const m = ('0' + (this.mounth + 1)).slice(-2)
        const d = ('0' + this.day).slice(-2)
        this.$input.textContent = `${d}.${m}.${this.year}`
        this.options.onSelect ? this.options.onSelect(`${d}.${m}.${this.year}`) : null
    }

    //–ø—Ä–∏–≤–∞—Ç–Ω—ã–π –º–µ—Ç–æ–¥, –∫–æ—Ç–æ—Ä—ã–π —Ä–∏—Å—É–µ—Ç
    //–ø—Ä–∏–≤–∞—Ç–Ω—ã–π, —ç—Ç–æ –∑–Ω–∞—á–∏—Ç —á—Ç–æ —Ç–æ–ª—å–∫–æ –∏–∑–Ω—É—Ç—Ä–∏ –º–æ–∂–µ—Ç –±—ã—Ç—å –≤—ã–∑–≤–∞–Ω
    #render() {
        //–¥–æ–±–∞–≤–∏—Ç—å –∫–ª–∞—Å—Å
        this.$el.classList.add('datepicker')
        //–¢—ã –∫–ª–∞–¥–µ—à—å –≤–Ω—É—Ç—å —ç—Ç–æ–≥–æ —Ç–µ–≥–∞/node –Ω—É–∂–Ω—ã–π —Ç–µ–±–µ —Ö—ç—Ç—ç–º—ç–ª—å
        this.$el.innerHTML = getTemplate()
    }

    #setup() {
        //–≠—Ç–æ –Ω—É–∂–Ω–æ, —á—Ç–æ–±—ã this –≤ handleClick —Ä–∞–±–æ—Ç–∞–ª–æ –∫–æ—Ä–µ–∫—Ç–Ω–æ
        this.handleClick = this.handleClick.bind(this)
        //–≤–µ—à–∞–µ–º —Å–ª—É—à–∞—Ç–µ–ª—è –∫–ª–∏–∫–æ–≤ –ø–æ –Ω–∞—à–µ–º—É —ç–ª–µ–º–µ–Ω—Ç—É
        this.$el.addEventListener('click', this.handleClick)

        this.$mounth = this.$el.querySelector('[data-type="mounth"]')
        this.$mounth.textContent = `${mounts[this.mounth]}, ${this.year}`

        this.$input = this.$el.querySelector('[data-type="input"]')
        const m = ('0' + (this.mounth + 1)).slice(-2)
        const d = ('0' + this.day).slice(-2)
        this.$input.textContent = `${d}.${m}.${this.year}`

        this.$content = this.$el.querySelector('[data-type="content"]')

        this.#renderDates()
    }

    #renderDates() {
        let str = ``
        for (let i = 0; i < this.first_wday - 1; i++) {
            str += `<div class="disabled"> </div>`
        }
        for (let i = 1; i < this.last_date; i++) {
            str += `<div data-type="date">${i}</div>`
        }
        this.$content.innerHTML = str
        // for (i=1;i < last_date;i++) {
        //     // var td_d = document.getElementsByTagName("td");
        //     if (i == first_wday) {
        //         td_d[0].innerHTML = mounts[month];
        //         td_d[0].bgColor = "ffcccc";
        //         td_d[0].align = "center";
        //         td_d[0].style.fontFamily = "arial black";
        //         td_d[7+i].innerHTML=1;

        //         for (j=0;j<last_date;j++)
        //             td_d[7+i+j].innerHTML = 1+j;

        //         for (c=8;c<43;c++)
        //             if (td_d[c].innerHTML==0) {
        //                 td_d[c].visibility = "hidden";
        //             } else td_d[c].bgColor = "white";

        //         for (z=0;z<last_date;z++)
        //             if (td_d[z].innerHTML == today) {
        //                 td_d[z].bgColor = "salmon";
        //             }
        //     }
        // }
    }

    //–û–±—Ä–∞–±–æ—Ç–∫–∞ –∫–ª–∏–∫–∞ –ø–æ –∫–æ—Ä–Ω–µ–≤–æ–º—É —ç–ª–µ–µ–º–µ–Ω—Ç—É
    handleClick(event) {
        // {} - —ç—Ç–æ –≤—ã—Ç—è—à–∏–≤–∞–Ω–∏–µ –Ω—É–∂–Ω—ã—Ö –∑–Ω–∞—á–µ–Ω–∏–π. –ú–æ–∂–Ω–æ —Ç–∞–∫ const type = event.target.dataset.type –ù–æ —É–∂–µ –æ–±—ã—á–Ω–æ —Ç–∞–∫ –Ω–µ –¥–µ–ª–∞—é—Ç –∏–∑-–∑–∞ –ø—Ä–∏–≤—ã—á–∫–∏, –∫–æ–≥–¥–∞ –Ω–µ—Å–∫–æ–ª—å–∫–æ –ø–µ—Ä–µ–º–µ–Ω–Ω—ã—Ö –Ω–∞–¥–æ –≤—ã—Ç–∞—â–∏—Ç—å
        const { type } = event.target.dataset
        //console.log(type, event) //–î–ï–ë–ê–ì –∫–ª–∏–∫–æ–≤
        if (type === 'input') {
            this.toogle()
        }
        if (type === 'before') {
            this.mounth -= 1
            if (this.mounth <= -1) {
                this.mounth = 11
                this.year -= 1
            }
            this.calcDates()
            this.#renderDates()
            this.$mounth.textContent = `${mounts[this.mounth]}, ${this.year}`
        }
        if (type === 'next') {
            this.mounth += 1
            if (this.mounth >= 12) {
                this.mounth = 0
                this.year += 1
            }
            this.calcDates()
            this.#renderDates()
            this.$mounth.textContent = `${mounts[this.mounth]}, ${this.year}`
        }

        if (type === 'date') {
            this.day = event.target.textContent
            this.select()
            this.toogle()
        }

        if (type === 'backdrop') {
            this.close()
        }
    }

    //
    destroy() {
        //–£–±–∏—Ä–∞–µ–º –∫–ª–∏–∫. –ù–∞–ø—Ä–º–µ—Ä, –∫–æ–≥–¥–∞ –æ—Ç–∫—Ä—ã–≤–∞–µ—Ç—Å—è –¥—Ä—É–≥–∞—è —Å—Ç—Ä–∞–Ω–∏—á–∫–∞, –≥–¥–µ –Ω–µ—Ç –Ω–∞—à–µ–≥–æ —ç–ª–µ–º–µ–Ω—Ç–∞
        this.$el.removeEventListener('click', this.handleClick)
    }

    toogle() {
        this.isOpen ? this.close() : this.open()
    }

    get isOpen() {
        return this.$el.classList.contains('open')
    }

    calcDates() {
        let first_day = new Date(this.year, this.mounth, 1) // —É—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –¥–∞—Ç—É –ø–µ—Ä–≤–æ–≥–æ —á–∏—Å–ª–∞ —Ç–µ–∫—É—â–µ–≥–æ –º–µ—Å—è—Ü–∞
        console.log(first_day)
        this.first_wday = first_day.getDay() // –∏–∑ –Ω–µ–µ –≤—ã—á–∏—Å–ª—è–µ–º –¥–µ–Ω—å –Ω–µ–¥–µ–ª–∏ –ø–µ—Ä–≤–æ–≥–æ —á–∏—Å–ª–∞ —Ç–µ–∫—É—â–µ–≥–æ –º–µ—Å—è—Ü–∞
        const oneHour = 1000 * 60 * 60 // –≤—ã—á–∏—Å–ª—è–µ–º –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –º–∏–ª–ª–∏—Å–µ–∫—É–Ω–¥ –≤ 1 —á–∞—Å–µ
        const oneDay = oneHour * 24 // –≤—ã—á–∏—Å–ª—è–µ–º –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –º–∏–ª–ª–∏—Å–µ–∫—É–Ω–¥ –≤ –æ–¥–Ω–∏—Ö —Å—É—Ç–∫–∞—Ö
        const nextMonth = new Date(this.year, this.mounth + 1, 1) // —É—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –¥–∞—Ç—É –ø–µ—Ä–≤–æ–≥–æ —á–∏—Å–ª–∞ —Å–ª–µ–¥—É—é—â–µ–≥–æ –º–µ—Å—è—Ü–∞
        this.last_date =
            Math.ceil((nextMonth.getTime() - first_day.getTime() - oneHour) / oneDay) + 1 // –≤—ã—á–∏—Å–ª—è–µ–º –∫—Ä–∞–π–Ω–∏–π –¥–µ–Ω—å —Ç–µ–∫—É—â–µ–≥–æ –º–µ—Å—è—Ü–∞
        console.log('DEBUG: ', this.first_wday)
    }
}
